import React, {Component} from 'react';
import Globaldig from './Globaldig'

export default class Global extends Component {
    state = {
        country: [],
        area: [],
        slct:'',
        dta:[]
    }

    change = (e) => {
        this.setState({slct: e.target.value})
        // console.log(e.target.value);
        fetch(`https://covid19.mathdro.id/api/countries/${e.target.value}`)
            .then(response => response.json())
            .then((result) => {
                this.setState({dta: result})
                // console.log(result.confirmed.value);

                

            }, (error) => {
                this.setState({error})
            })
        
    }

    componentDidMount()
    {
        // const apiurl = `https://covid19.mathdro.id/api`;

        fetch(`https://covid19.mathdro.id/api/countries`)
            .then(response => response.json())
            .then((result) => {
                this.setState({country: result.countries})

            }, (error) => {
                this.setState({error})
            })
    }

    render(props) {
        const {country, area,slct,dta} = this.state;
        // console.log(slct);

        
        // if(dta)
        // {
        //     console.log(dta);

            
        // }

        

        if (country) {
             
            country.map((a, i) => {
                area[i]=a.name
                return 0
            })
        }
        

        return (
            <div className="d-flex justify-content-center align-items-center">
                <div className="">
                    <div className="">
                        {area
                            ? <div className="">
                                    <div className="text-center">
                                        <select
                                            name=""
                                            defaultValue={0}
                                            id=""
                                            onChange={this.change}
                                            className="col-col-12"
                                            style={{
                                            borderBottom: "3px solid black"
                                        }}>
                                            {area.map((s, i) => <option key={i} value={s}>
                                              {s}
                                            </option>)}
                                        </select>
                                    </div>
                                    <Globaldig dig={dta} inf={slct}/>
                                                    {/* <Globaldig dig={slct}/> */}
                                        {/* {getdata(slct)} */}

                                </div>

                            : null}
                    </div>
                </div>
                
            </div>
        )
    }
}
