import React, {Component} from 'react';
import {HashRouter, Route} from 'react-router-dom'
import Api from './components/Api';
import Distric from './components/Distric'
import Main from './components/Main'
import News from './components/News/News'
import Reso from './components/resources/Res'
import Navb from '../src/components/Navb'
import Global from './components/global/Global';
import FooterPage from './components/FooterPage'
import logo from '../src/img/img.png';
import Test from './components/Test'
import ParticlesBg from 'particles-bg'

class App extends Component {

    render() {
        return (
            <HashRouter>
                <ParticlesBg
                    color=""
                    type="tadpole"
                    bg={true}
                    className="cobweb"
                    style={{
                    position: "absolute",
                    zIndex: -1,
                    top: 0,
                    left: 0,
                    bottom: -20
                }}/>
                <Navb/>

                <img src={logo} alt="" className="center-block img-fluid"/>
                <Route exact path='/'>
                    <Main/>
                </Route>
                <Route path='/Distric'>
                    <Distric/>
                </Route>
                <Route path='/Api'>
                    <Api/>
                </Route>

                <Route path='/News'>
                    <News/>
                </Route>
                <Route path='/Reso'>
                    <Reso/>
                </Route>

                <Route path='/Global'>
                    <Global/>
                </Route>

                <Route path='/Test'>
                    <Test/>
                </Route>
                <FooterPage/>
            </HashRouter>
        )
    }
}

export default App;
