import React from "react";
import {MDBContainer, MDBFooter} from "mdbreact";

const FooterPage = () => {
    return (
        <MDBFooter className="font-small bg-dark text-light sticky-bottom pb-1">
            <span className="footer-copyright text-center">
                <MDBContainer fluid className="bg-dark">
                    <span className="text-capitalize text-muted">
                        made with &nbsp; &nbsp;
                        <svg
                            class="bi bi-heart-fill sze"
                            width="1em"
                            height="1em"
                            viewBox="0 0 16 16"
                            fill="currentColor"
                            xmlns="http://www.w3.org/2000/svg">
                            <path
                                fill-rule="evenodd"
                                d="M8 1.314C12.438-3.248 23.534 4.735 8 15-7.534 4.736 3.562-3.248 8 1.314z"/>
                        </svg>
                    </span>
                    <hr/>
                        <span role="img" aria-label="" className="fnts">
                            &copy; Copyright : &nbsp; 😂😂😂
                        </span>
                </MDBContainer>
            </span>
        </MDBFooter>
    );
}

export default FooterPage;