import React, {Component} from 'react';
import CountUp from 'react-countup';
// import {Link} from 'react-router-dom';
// import Navb from './Navb'




export default class Distcard extends Component {

    render(props) {
        console.log(this.props.data);

        return (
            <div className="">
                {this.props.data
                    ? <div className="dwn rsp">

                            <div className="d-flex justify-content-center mb-0">

                                <div className="text-center">
                                    <ul className="text-dark">
                                        <li className="alert alert-danger text-capitalize ">active : &nbsp; &nbsp;
                                            <span className="card-text fnt">{< CountUp start ={
                                                0
                                            }
                                            end = {
                                                this.props.data.active
                                            }
                                            duration = {
                                                1.5
                                            }
                                            separator = ',' />}</span>

                                        </li>
                                        <li className="alert alert-primary text-capitalize">deaths : &nbsp; &nbsp;
                                            <span className="card-text fnt">{< CountUp start = {
                                                0
                                            }
                                            end = {
                                                this.props.data.deceased
                                            }
                                            duration = {
                                                1.5
                                            }
                                            separator = ',' />}</span>
                                        </li>
                                        <li className="alert alert-success text-capitalize">recovered : &nbsp; 
                                            <span className="card-text fnt">{< CountUp start = {
                                                0
                                            }
                                            end = {
                                                this.props.data.confirmed
                                            }
                                            duration = {
                                                1.5
                                            }
                                            separator = ',' />}</span>
                                        </li>
                                        <li className="alert alert-warning text-capitalize">last updated on :
                                            <p className="text-capitalize mb=0 pb-0">
                                                unavailable</p>
                                        </li>

                                    </ul>
                                </div>
                            </div>
                        </div>
                    : <div>
                        <div className="dwn rsp">
                            <div className="d-flex justify-content-center mb-0">

                                <div className="text-center">
                                    <ul className="text-dark">
                                        <li className="alert alert-danger text-capitalize ">confirmed :&nbsp; &nbsp; &nbsp; &nbsp; &nbsp;||
                                        </li>
                                        <li className="alert alert-primary text-capitalize">deaths : &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;||
                                        </li>
                                        <li className="alert alert-success text-capitalize">recovered : &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;||
                                        </li>
                                        <li className="alert alert-warning text-capitalize">last updated on : ||
                                        </li>

                                    </ul>
                                </div> 
                            </div>
                        </div>
                    </div>
}
            </div>
        )
    }
}
