import React, {Component} from 'react'
import {Link} from 'react-router-dom';
import ParticlesBg from 'particles-bg'

export default class Test extends Component {
    render() {
        return (
            <div className="dwn rsp">
                <ParticlesBg
                    color=""
                    type="tadpole"
                    bg={true}
                    className="cobweb"
                    style={{
                    position: "absolute",
                    zIndex: -1,
                    top: 0,
                    left: 0,
                    bottom:-20
                }}/>
                <div className="d-flex justify-content-center mb-0">

                    <div className="text-center">
                        <ul className="text-light">
                            <li className="alert alert-primary text-capitalize ">check country wise updates : &nbsp; &nbsp;
                                <Link to='/Global' className='text-dark card-body col card-subtitle'>
                                    <button className='btn btn-md btn-primary'>
                                        CHECK
                                    </button>
                                </Link>
                            </li>
                            <li className="alert alert-primary text-capitalize">get State wise corona update : &nbsp;
                                <Link to='/Global' className='text-dark card-body col card-subtitle'>
                                    <button className='btn btn-md btn-primary'>
                                        CHECK
                                    </button>
                                </Link>
                            </li>
                            <li className="alert alert-primary text-capitalize">get distric wise corona update :
                                <Link to='/Global' className='text-dark card-body col card-subtitle'>
                                    <button className='btn btn-md btn-primary'>
                                        CHECK
                                    </button>
                                </Link>
                            </li>
                            <li className="alert alert-primary text-capitalize">resources and testing labs : &nbsp; &nbsp; &nbsp;
                                <Link to='/Global' className='text-dark card-body col card-subtitle'>
                                    <button className='btn btn-md btn-primary'>
                                        CHECK
                                    </button>
                                </Link>
                            </li>

                            <li className="alert alert-primary text-capitalize">get covid news updates : &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;
                                <Link to='/Global' className='text-dark card-body col card-subtitle'>
                                    <button className='btn btn-md btn-primary'>
                                        CHECK
                                    </button>
                                </Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}
