import React, {Component} from 'react';
import Distcard from './Distcard';
// import Navb from './Navb'


class Districdig extends Component {

    state = {
        dist: ''
    }

    change = (e) => {
        console.log(e.target.value);
        this.setState({dist: e.target.value})
    }

    render(props) {
        const {dist} = this.state
        if (this.props.dig) {}

        return (
            <div
                className="d-flex container-fluid justify-content-center  align-items-center">
                {this.props.dig
                    ? <div className="">
                            <div className="text-center">
                                <select
                                    name=""
                                    defaultValue={0}
                                    id=""
                                    onChange={this.change}
                                    className=""
                                    style={{
                                    borderBottom: "3px solid black",
                                    width: "260px"
                                }}>
                                    {this
                                        .props
                                        .dig
                                        .districtData
                                        .map((s, i) => <option key={i} value={i}>
                                            {s.district}
                                        </option>)}
                                </select>
                            </div>
                            <div className="">
                                <Distcard className="" data={this.props.dig.districtData[dist]}/>
                            </div>
                        </div>
                    :  <div>
                        <div className="dwn rsp">
                            <div className="d-flex justify-content-center mb-0">

                                <div className="text-center">
                                    <ul className="text-dark">
                                        <li className="alert alert-danger text-capitalize ">confirmed :&nbsp; &nbsp; &nbsp; &nbsp; &nbsp;||
                                        </li>
                                        <li className="alert alert-primary text-capitalize">deaths : &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;||
                                        </li>
                                        <li className="alert alert-success text-capitalize">recovered : &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;||
                                        </li>
                                        <li className="alert alert-warning text-capitalize">last updated on : ||
                                        </li>

                                    </ul>
                                </div> 
                            </div>
                        </div>
                    </div>}
            </div>
        );
    }
}

export default Districdig;
